          {
            label: "Index comm",
            exportable: false,
            colSpan: 2,
          },
          {
            label: "Mobile comm",
            exportable: false,
            colSpan: 2,
          },
          {
            label: "SNS comm",
            exportable: false,
            colSpan: 2,
          },
          {
            label: "VCT Comm",
            exportable: false,
            colSpan: 2,
          },
          {
            label: "Other Comm TP",
